/* Elements
--------------------------------------------- */
html,
body {
  background-color: #f9f9f9;
  width: 100%;
  height: 100%;
}

hr {
  background-color: #c4c4c4;
  border: 0;
  height: 0.0625rem;
  margin-bottom: 1.5em;
}

ul,
ol {
  margin: 0 0 1.5em 3em;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: 700;
}

dd {
  margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object,
video {
  max-width: 100%;
}

img {
  height: auto;
  max-width: 100%;
}

figure {
  margin: 1em 0;
}

table {
  margin: 0 0 1.5em;
  width: 100%;
}

/* Links
--------------------------------------------- */
a:not(.button) {
  color: #000000;
  transition: color 300ms cubic-bezier(0.62, 0.01, 0.37, 0.99);
}
a:not(.button):visited {
  color: #e7131d;
}
a:not(.button):hover, a:not(.button):focus, a:not(.button):active {
  color: #333333;
  text-decoration: none;
}
a:not(.button):focus {
  outline: thin dotted;
}
a:not(.button):hover, a:not(.button):active {
  outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit],
.button {
  display: flex;
  align-items: center;
  padding: 0.75rem 1.875rem 0.625rem;
  border-width: 0.125rem;
  border-style: solid;
  border-color: #a9a9a9;
  border-radius: 0.25rem;
  color: #a9a9a9;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.5;
  font-family: "Univers LT Std Condensed", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;
  letter-spacing: 0.01em;
  background: #a9a9a9;
  transition: all 300ms cubic-bezier(0.62, 0.01, 0.37, 0.99);
  transition-property: background, border-color;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover,
.button:hover {
  cursor: pointer;
}
button[disabled],
input[type=button][disabled],
input[type=reset][disabled],
input[type=submit][disabled],
.button[disabled] {
  color: #a9a9a9;
  background: none;
}
button[disabled]:hover,
input[type=button][disabled]:hover,
input[type=reset][disabled]:hover,
input[type=submit][disabled]:hover,
.button[disabled]:hover {
  cursor: not-allowed;
}

.button--red {
  background-color: #e7131d;
  border-color: #e7131d;
  color: #ffffff;
}
.button--red:hover, .button--red:focus {
  background: #ffffff;
  color: #e7131d;
}
.button--red-outline {
  background: none;
  border-color: #e7131d;
  color: #e7131d;
}
.button--red-outline:hover {
  background: #e7131d;
  color: #ffffff;
}
.button--red-outline:focus {
  outline: #e7131d;
  background: #e7131d;
  color: #ffffff;
}
.button--white {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #000000;
}
.button--white:hover, .button--white:focus {
  background: #000000;
  border-color: #000000;
  color: #ffffff;
}
.button--white-outline {
  background: none;
  border-color: #ffffff;
  color: #ffffff;
}
.button--white-outline:hover {
  background: #ffffff;
  color: #000000;
}
.button--white-outline:focus {
  outline: #e6e6e6 auto 1px;
  background: #ffffff;
  color: #000000;
}
.button--nav {
  background: none;
  border: none;
  padding: 0.75rem 0;
  color: rgba(0, 0, 0, 0.6);
}
.button--nav:hover {
  color: #e7131d;
}
.button--nav:focus, .button--nav:focus-visible {
  background-image: linear-gradient(to right, #e7131d 0%, #e7131d 100%);
  background-repeat: repeat-x;
  background-position: 0 100%;
  background-size: 100% 2px;
  outline: none;
  color: #e7131d;
}
.button--nav.active {
  background-image: linear-gradient(to right, #e7131d 0%, #e7131d 100%);
  background-repeat: repeat-x;
  background-position: 0 100%;
  background-size: 100% 2px;
  color: #e7131d;
}
.button--link {
  background: none;
  border: none;
  padding: 0.75rem 0;
  color: #e7131d;
  text-transform: none;
}
.button--link:hover, .button--link:focus, .button--link:focus-visible, .button--link.active {
  outline: none;
  color: #e7131d;
  text-decoration: underline;
}
.button--thin {
  padding: 0.4375rem 1.625rem 0.3125rem;
}

